:root {
  --primary-color: #00e599;
  --secondary-color: #2b2d42;
  --background-color: #f0f2f5;
  --text-color: #333;
  --card-background: #ffffff;
  --shadow-color: rgba(0, 0, 0, 0.1);
}

body {
  margin: 0;
  font-family: 'Roboto', 'Arial', sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.App-header {
  background-color: var(--secondary-color);
  padding: 2rem;
  color: white;
  box-shadow: 0 2px 4px var(--shadow-color);
}

.App-header h1 {
  margin: 0;
  font-size: 2.5rem;
  font-weight: 700;
}

.wallet-status {
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wallet-status p {
  margin-right: 1rem;
}

.network-warning {
  color: #ff6b6b;
  font-weight: bold;
  margin-top: 1rem;
  background-color: rgba(255, 107, 107, 0.1);
  padding: 0.5rem;
  border-radius: 4px;
}

.App-main {
  flex-grow: 1;
  max-width: 800px;
  margin: 2rem auto;
  padding: 0 1rem;
}

.card {
  background-color: var(--card-background);
  border-radius: 8px;
  padding: 2rem;
  margin-bottom: 2rem;
  box-shadow: 0 4px 6px var(--shadow-color);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 8px var(--shadow-color);
}

.voting-options {
  display: flex;
  justify-content: space-around;
  margin-top: 2rem;
}

.option {
  flex-basis: 45%;
  padding: 1rem;
  border-radius: 8px;
  background-color: rgba(0, 229, 153, 0.1);
  transition: background-color 0.3s ease;
}

.option:hover {
  background-color: rgba(0, 229, 153, 0.2);
}

.btn {
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
}

.btn-primary {
  background-color: var(--primary-color);
  color: var(--secondary-color);
}

.btn-secondary {
  background-color: #6c757d;
  color: white;
}

.btn-vote {
  background-color: var(--secondary-color);
  color: white;
  font-size: 1.2rem;
  padding: 1rem 2rem;
}

.btn:hover:not(:disabled) {
  opacity: 0.9;
  transform: translateY(-2px);
}

.btn:disabled {
  background-color: #cccccc;
  color: #666666;
  cursor: not-allowed;
}

.vote-count {
  font-size: 1.2rem;
  font-weight: bold;
  margin-top: 1rem;
  color: var(--secondary-color);
}

.App-footer {
  background-color: var(--secondary-color);
  color: white;
  padding: 1rem;
  text-align: center;
  font-size: 0.9rem;
}

@media (max-width: 600px) {
  .voting-options {
    flex-direction: column;
  }

  .option {
    margin-bottom: 1rem;
  }
}